import OpenList from "../OpenList";


const Planimetry = ({page}) => { 
    
    const facts = {
        '': [
            {
                name: '',
                text: ' -   ,      '
            },
            {
                name: ' ',
                text: '  - ,        '
            },
            {
                name: ' ',
                text: '  -        '
            },
            {
                name: '',
                text: '  - ,       '
            },
            {
                name: ' ',
                text: '  - ,      '
            },
            {
                name: ' ',
                text: '  - ,     ,   '
            },
            {
                name: ' ',
                text: '  - ,    '
            }
        ],
        ', ': [
            {
                name: '  ',
                text: '           2  1,   '
            },
            {
                name: '   ',
                text: '      '
            }
        ],
        '': [
            {
                name: ' ',
                text: '  '
            },
            {
                name: ' ',
                text: '          '
            },
            {
                name: ',  ,  ',
                text: `           : <br><br>
                1.    <br>
                2.     <br>
                3.      \\(180^{\\circ}\\),<br><br>
                   
                `
            },
            {
                name: '  ',
                text: '   \\(n\\)-  \\(180^{\\circ}\\cdot (n-2)\\)'
            }
        ],
        '':[
            {
                name: '  ',
                text: `
                1.    <br>
                2.   <br>
                3.     \\(180^{\\circ}\\)`
            },
            {
                name: ' ',
                text: `        <br>
                    ,        `
            },
            {
                name: '   ',
                text: `    ,<br>
                       `
            }
        ],
        ' ': [
            {
                name: '  ',
                text: `  ,          :<br><br>
                1.  2     <br>
                2.    2    <br>
                3.  3 `
            },
            {
                name: '  ',
                text: `  ,          :<br><br>
                1.  2 <br>
                2.    2     <br>
                3. 3  `
            },
            {
                name: '  ',
                text: '    \\(180^{\\circ}\\)'
            },
            {
                name: ' ',
                text: '[  ]'
            },
            {
                name: ' ',
                text: '[  ]'
            }
        ]
    };

    const parse = facts => {
        var keys = Object.keys(facts);
        var array = [];
        keys.forEach(key => {
            array.push({title: key, facts: facts[key]});
        });
        return array;
    };

    return page(
    <div>
        <h1 onClick={page.index} className='main-menu'></h1>
        <ul>
        {
            parse(facts).map((facts_group,index) =>
                <li key={index}>
                    <OpenList
                        header = {<div className="main-menu">{facts_group.title}</div>}
                        list = {
                            <ol>
                                {
                                    facts_group.facts.map((fact,index) =>
                                        <li key={index}>
                                            <OpenList
                                                header = {<div className="sub-menu">{fact.name}</div>}
                                                list = {
                                                    <div className="frame">
                                                        <h3 style={{marginTop:'-7px'}}>:</h3>
                                                        {
                                                            fact.text.split('<br>').map((str, index) =>
                                                                <div key={index}>{str===''?<span>&nbsp;</span>:str}</div>    
                                                            )
                                                        }
                                                    </div>
                                                }
                                            />
                                        </li>    
                                    )
                                }
                            </ol>
                        }
                    />
                </li>
            )
        }
        </ul>
    </div>
)};

export default Planimetry;